.header {
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        padding: 20px 0;
    }

    &__left {
        flex-basis: 33%
    }

    &__right {
        flex-basis: 67%;
        display: flex;
        flex-direction: column;


    }



    &__logo {
        display: flex;
        align-items: center;
    }

    &__img {
        margin-right: 15px;
        flex-shrink: 0;
    }

    &__logo-desc {
        font-size: 16px;
        color: #292828;
        line-height: 1.3;

    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 7px;

    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__info {
        display: flex;
        margin-right: 15px;

        span {
            color: #787878;
            font-size: 14px;
            line-height: 1.3;
        }

    }

    &__ic {
        width: 17px;
        height: 17px;
        flex-shrink: 0;
    }

    &__phone {
        font-size: 18px;
        font-weight: 700;
        color: #000;
        text-decoration: none;
    }

    &__btn {
        border-radius: 4px;
        background: $blue;
        color: #fff;
        padding: 12px 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        cursor: pointer;
        margin-left: 10px;

        &:hover {
            background: #5EAD25;
        }
    }

    &__bluebtn {
        padding-left: 7px;
        cursor: pointer;
        height: 40px;
    }

    &__lk-mobile {
        display: none;
    }

}


@media only screen and (max-width : 1200px) {
    .header {
        &__logo-desc {
            font-size: 12px;
        }

        &__info {
            span {
                font-size: 12px;
            }
        }

        &__phone {
            font-size: 16px;
        }
    }
}

.desktop-hidden {
    display: none;
}

@media only screen and (max-width : 1024px) {
    .desktop-hidden {
        display: flex;
        flex-direction: column;
        margin: 20px 0 30px 0;
    }

    .mobile-phones {
        display: flex;
        flex-direction: column;

        margin-bottom: 30px;

        a {
            margin-bottom: 10px;
        }
    }

    .header {
        &__top {
            display: none;
        }

        &__bottom {
            justify-content: flex-end;
        }


    }

    .mobile-info {
        margin-bottom: 15px;
    }

}

@media only screen and (max-width : 768px) {
    .header {
        &__btn {
            display: none;
        }

        &__lk-mobile {
            display: flex;
            justify-content: space-between;

            .header__icon-lk {
                fill: #000;
            }

            .cart-txt {
                display: none;
            }

            .header__cart {
                margin: 0 10px;
            }
        }

        &__logo-desc {
            display: none;
        }

    }


}

