.newsect001 {
    margin: 30px 0;
}

.section001-title {
    font-size: 28px;
    font-weight: 600;
    margin: 10px 0;
}


@media only screen and (max-width : 576px) {
    .section001-title {
        font-size: 18px;

    }

}


.product_card {
    min-height: 350px;
    background: #fff;
    padding: 15px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 14px 21px 0px rgba(0, 0, 0, 0.07);
    transition: .3s;
    display: flex;
    flex-direction: column;
    margin: 15px 0;




    .product_image {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
        max-height: 150px;

    }

    .product_info {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        color: gray;
        font-size: 12px;
        margin-bottom: 20px;
    }

    .product_name {
        font-size: 14px;
        line-height: 1.3;

        margin-bottom: 30px;
        color: $black;
        font-weight: 600;

    }

    .product_buy {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        align-items: center;

        .price {
            font-weight: 700;
            font-size: 20px;
            color: $black;
        }

        .cart_btn {
            background: $green;
            padding: 10px 20px;
            border: none;
            transition: .3s;
            width: 63px;
            height: 43px;

            .ic-cart {
                width: 23px;
                height: 23px;
                fill: #fff;
            }

            &:hover {
                opacity: .7;
                transition: .3s;
            }
        }
    }

    &:hover {
        transition: .3s;
        box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.07);

    }

}


@media only screen and (max-width : 1199px) {
    .category_block2 {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .category_block1 {
        flex: 0 0 100%;
        max-width: 100%;

        .choose_z {
            display: none;
        }
    }

}

.special-block {
    position: relative;
    z-index: 90;

    &__price {
        background: yellow;
        font-size: 12px;
        color: #000;
        padding: 4px 8px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        margin: 15px 0;
        cursor: pointer;
    }

    &__hidden {
        opacity: 0;
        position: absolute;
        left: 0;
        right: 0;
        background: #fff;
        box-shadow: 0 5px 15px #ececec;
        padding: 15px;
        border-radius: 4px;
        font-size: 12px;
        line-height: 1.4;
        min-width: 250px;


    }

    &__hidden-phone {
        display: block;
        padding: 7px 0;
        font-weight: 700;
        font-size: 14px;
    }

    &:hover .special-block__hidden {
        opacity: 1;
    }
}


.btn--or {
    position: relative;
    display: inline-flex;
    background: #fff;
    margin: 7px 0;
    border: 2px solid #ececec;
    padding: 7px 15px;

    .ic_filters {
        width: 15px;
        height: 15px;
        fill: $green;

    }

    span {
        font-size: 14px;
        margin-left: 10px;
    }

}
