.nav__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
}

.nav__item:not(:last-child) {
    margin-right: 18px;
}

.nav__link {
    font-size: 15px;
    color: $black;
    text-transform: uppercase;
    font-weight: 600;


    &:hover {
        color: $blue;
    }
}

.burger {
    position: relative;
    width: 30px;
    height: 20px;
    display: none;
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}

.burger__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 2px;
    background-color: #000;
    transform: translateY(-50%);
    transition: opacity 0.3s ease-in-out;
}

.burger::before,
.burger::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out;
}

.burger::before {
    top: 0;
}

.burger::after {
    bottom: 0;
}

.burger--active .burger__line {
    opacity: 0;
}

.burger--active::before {
    top: 50%;
    transform: rotate(45deg);
}

.burger--active::after {
    bottom: auto;
    top: 50%;
    transform: rotate(-45deg);
}

.stop-scroll {
    overflow: hidden;
}

@media (max-width: 1024px) {
    .burger {
        display: block;
    }

    .nav {

        position: fixed;
        right: 0;
        top: 70px;
        z-index: 1000;
        overflow-y: auto;
        padding: 0;
        width: 60%;
        max-width: 300px;
        /* height: 100vh;*/
        background-color: #fff;
        transform: translateX(100%);
        transition: transform 0.6s ease-in-out;
    }

    .nav--visible {
        transform: none;
    }

    .nav__list {
        flex-direction: column;


    }

    .nav__item {
        width: 100%;
    }

    .nav__item:not(:last-child) {
        margin-right: 0;
    }

    .nav__link {
        display: inline-block;
        width: 100%;
        padding: 20px 30px;
        border-bottom: 1px solid #ececec;
        font-size: 20px;
    }
}

@media only screen and (max-width : 1200px) {
    .nav__link {
        font-size: 12px;
    }

    .nav__item:not(:last-child) {
        margin-right: 12px;
    }

}


.nav__list {
    .active {
        .nav__link {
            color: $blue;
        }
    }
}
