.header {
    &__catalog {
        background: linear-gradient(0deg, #549625 2.08%, #61B425 97.92%);
        padding: 7px 0;

    }

    &__icon-white {
        width: 20px;
        height: 20px;
    }

    &__bluebtn {
        background: $blue;
        flex-basis: 15%;
    }

    &__form {
        flex-basis: 65%;
        position: relative;
    }

    &__lk {
        flex-basis: 20%;
        display: flex;
        justify-content: space-between;
        padding-left: 20px;
    }

    &__catalog-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }

    &__search {
        background: #ffff;
        width: 100%;
        height: 40px;
        border: none;
        box-shadow: none;
        border-radius: 4px;
        padding-left: 20px;


    }

    &__search-btn {
        background: transparent;
        border: none;
        box-shadow: none;
        outline: none;
        position: absolute;
        right: 15px;
        z-index: 20;
        top: 7px;
    }

    &__icon-search {
        width: 25px;
        height: 25px;
        fill: #D8D8D8;
    }

    &__lk {
        display: flex;
    }

    &__icon-lk {
        width: 25px;
        height: 25px;
        fill: #fff;
    }

    &__cart {
        position: relative;
        color: #fff;
        opacity: .9;

        &:hover {
            color: #fff;
            text-decoration: none;
            opacity: 1;
        }
    }

    &__amount {
        position: absolute;
        right: -15px;
        bottom: -10px;
        background: red;
        border-radius: 50%;
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 11px;
    }
}


@media only screen and (max-width : 1200px) {

    .header {
        &__bluebtn {
            flex-basis: 15%;
        }

        &__form {
            flex-basis: 50%;
        }

        &__lk {
            flex-basis: 25%;
            ;

        }

    }
}


@media only screen and (max-width : 991px) {

    .header {
        &__catalog {
            margin-bottom: 60px
        }

        &__bluebtn {
            position: absolute;
            left: 0;
            bottom: -50px;
            width: 100%;
        }

        &__form {
            flex-basis: 60%;
        }

        &__lk {
            flex-basis: 40%;


        }
    }
}

@media only screen and (max-width : 768px) {
    .header {
        &__lk {
            display: none;
        }

        &__form {
            flex-basis: 100%;
        }
    }


}
